import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { dismissItems } from '../actions/itemActions';
import { connect } from 'react-redux';
import { LISTS_URL } from '../listrUrls';
import PropTypes from 'prop-types'

class ListDetail extends Component {

  constructor(props){
    super(props);
    this.dismissItems = this.dismissItems.bind(this);
  }

  dismissItems(){
    this.props.dismissItems();
  }

  render () {
      const data = this.props.items.map(item => (
        <li key={item.static_id}>{item.name}</li>
      ));
      return(
        <Modal isOpen={this.props.isOpen} toggle={this.props.toggle} className={this.props.className} centered={true}>
          <ModalHeader toggle={this.props.toggle}>
            {this.props.data.name}
          </ModalHeader>
          <ModalBody>
            {data}
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.props.toggle}>Cancel</Button>
          </ModalFooter>
        </Modal>
      );
  }
}

ListDetail.proptypes = {
  dismissItems: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
});

export default connect(mapStateToProps, { dismissItems })(ListDetail);



